.container {
  display: flex;
  justify-content: center;

  .search {
    position: relative;
    width: 900px;

    .icon {
      width: 24px;
      height: 24px;
      opacity: 0.5;
      position: absolute;
      left: 5px;
      top: 6px;
    }

    .input {
      padding: 12px 35px;
      width: 100%;
      height: 36px;
      background: #FFFFFF;
      border: 1px solid #D9D9D9;
      border-radius: 12px;
      font-size: 16px;
    }
  }
}
